import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        loading: false,
        showMenu: false,
    },
    mutations: {
        updateLoadingState(state, payload) {
            state.loading = payload;
        },
        toggleMenu(state, payload) {
            state.showMenu = payload;
        }
    },
})
